@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500&display=swap);
.select-mode{padding-left:0.7em;background-color:#cfd8dc;color:#7c8e94}

.play-button{background-color:#7a8d93;color:white;font-weight:500}@media (orientation: landscape){.play-button{max-width:8em}}

.text-field{padding-left:0.9em;background-color:#f3f3f3;color:#829398}

.message{font-size:1.4em;padding:1.2em 0;text-align:center;font-weight:500}@media (orientation: landscape){.message{font-size:1.6em;padding:1em 0}}

.table{position:relative;max-width:100%;padding-top:100%;border:solid 1px #f0f0f2}.table>tbody{position:absolute;top:0;left:0;bottom:0;right:0}.table *{height:100%}.table tr{flex-direction:row}.table td{border:solid 1px #f0f0f2}@media (orientation: landscape){.table{max-width:15em;padding-top:15em}}

.game{justify-content:flex-start;width:100%;height:50%}.controls{font-size:1.1em;height:10em}.controls>*{margin:0;margin-top:0.5em;border:solid transparent 1px;border-radius:0.3em;height:100%}.controls>*:first-child{margin-top:0}@media (orientation: landscape){.game{justify-content:center;width:50%;padding:2em}.controls{font-size:1.2em;flex-direction:row;height:3em}.controls>*{margin:0;margin-left:0.5em}.controls>*:hover{box-shadow:0px 0px 10px 2px #ebebeb}.controls>*:first-child{margin-left:0}}

.winners{width:100%}.winners>h3{font-size:1.8em;font-weight:500;color:#888888;padding:0.7em 0}.winners li{font-size:1.1em;font-weight:500;display:flex;justify-content:space-between;align-items:center;flex-direction:row;background-color:#cfd8dc;padding:0.7em 1em;color:#9ea7aa;margin-top:0.1em}.winners li:first-child{margin-top:0}@media (orientation: landscape){.winners{width:50%;height:100%;padding:1em 3.5em}.winners>h3{align-items:flex-start;padding:0;padding-bottom:0.7em}.winners>ul{justify-content:flex-start;height:20em;overflow:auto}}

/*
  Flexible - makes elements flexible by default.
  Description: https://github.com/ekb1zh/flexible
*/

address,
article,
aside,
blockquote,
body,
caption,
col,
colgroup,
dd,
details,
div,
dl,
dt,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
hr,
html,
legend,
main,
menu,
nav,
ol,
optgroup,
option,
p,
pre,
section,
summary,
table,
tbody,
td,
tfoot,
th,
thead,
tr,
ul {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
:root{box-sizing:border-box}*,::before,::after{box-sizing:inherit;margin:0;padding:0;width:100%;outline:none}body{font-size:calc(1vw + 1rem);font-family:'Ubuntu', sans-serif;padding:1em}.separator{height:0;width:100%;border-bottom:solid 2px #eeeef0}.hidden{display:none}@media (orientation: landscape){html,body{height:100%}body{font-size:calc(1vh + 0.5rem)}.container{flex-direction:row;height:100%;border:solid 2px #eeeef0}.separator{display:flex;height:100%;width:0;border-left:solid 2px #eeeef0}}

